'use client'

import di from '@/DependencyInjection';
import AuthenticatedPage from '@components/AuthenticatedPage';
import ProjectListItem from '@components/project/ProjectListItem';
import { UserServiceId } from '@interfaces/IUser';
import { ProjectServiceServiceId } from '@interfaces/services/IProjectService';
import { Add } from '@mui/icons-material';
import { Box, Divider, IconButton, List, ListItem, ListItemText, Paper, Typography } from '@mui/material';
import { type ProjectData } from '@strife/common';
import { JSX, useEffect, useState } from 'react';

export default function ProjectsPage(): JSX.Element {
  const user = di.get(UserServiceId);
  const projectService = di.get(ProjectServiceServiceId);
  const [ projectElements, setProjectElements ] = useState<JSX.Element[]>([]);
  useEffect(initializationEffect, []);

  const content = projectElements.length > 0
    ? projectElements
    : <ListItem sx={{ justifyContent: 'center', textAlign: 'center' }}>
        <ListItemText>You haven't created any projects yet.</ListItemText>
      </ListItem>

  return (
    <AuthenticatedPage>
      <Box sx={{ display: 'flex', width: '100vw', justifyContent: 'center', alignItems: 'center', padding: '16px' }}>
        <Paper sx={{ width: { xs: '100%', md: '1024px' }, height: '100%' }}>
          <Box display='flex' alignItems='center' justifyContent='space-between' padding={2}>
            <Typography variant='h6'>{user.account?.username}'s Projects</Typography>
            <IconButton>
              <Add />
            </IconButton>
          </Box>
          <Divider />
          <List >
            {content}
          </List>
        </Paper>
      </Box>
    </AuthenticatedPage>
  );

  function initializationEffect(): void {
    fetchProjects();
  }

  async function fetchProjects(): Promise<void> {
    const projects = await projectService.getProjects({ account_id: user.account?.id });
    const projectElements = projects.map((project: ProjectData, index: number): JSX.Element => <ProjectListItem key={index} project={project} />)
    setProjectElements(projectElements);
  }
}
