'use client'

import { AppBar, Avatar, Box, Toolbar, Typography, Button, IconButton, Link } from '@mui/material';
import { Menu } from '@mui/icons-material';
import Logo from '@components/Logo';
import { JSX } from 'react';
import di from '@/DependencyInjection';
import { UserServiceId } from '@interfaces/IUser';
import AccountAvatar from '@components/account/AccountAvatar';

export default function NavBar(): JSX.Element {
  const user = di.get(UserServiceId);

  return <Box sx={{ flexGrow: 1 }}>
    <AppBar position='static'>
      <Toolbar>
        <IconButton size='large' edge='start' color='inherit' aria-label='menu' sx={{ mr: 2 }}>
          <Menu />
        </IconButton>
        <Logo align='left' variant='h6' sx={{ flexGrow: 1 }}/>
        {
          user
            ? <Avatar>{user.account?.username?.charAt(0)}</Avatar>
            : <Button color='inherit' href='/login'>Login</Button>
        }
      </Toolbar>
    </AppBar>
  </Box>;
}
